import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import {Link} from "react-router-dom";
import Nav from "react-bootstrap/Nav";

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    title: {
        flexGrow: 1,
    },
}));

export default function NavTop() {
    const classes = useStyles();

    return (
        <div className="bgappbar d-lg-inline-block d-none">
            <AppBar className="bgappbar">
                <Toolbar className="bgappbar">
                    <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
                        <MenuIcon />
                    </IconButton>
                    <Typography variant="h6" className={classes.title}>
                        News
                    </Typography>
                    <Button className="text-white p-0">
                        <Link to="/" className="text-white p-3">خانه</Link>
                    </Button>
                    <Button className="text-white p-0">
                        <Link to="/type" className="text-white p-3">دسته بندی</Link>
                    </Button>
                    <Button className="text-white p-0">
                        <Link to="/countrys" className="text-white p-3">کشور ها</Link>

                    </Button>
                </Toolbar>
            </AppBar>
        </div>
    );
}
