import React from "react";
import 'bootstrap/dist/css/bootstrap.min.css';
import Search from "./search";
import Food from "./food";
import {Link} from "react-router-dom";
import CardActionArea from '@material-ui/core/CardActionArea';
import CardSmal from "./Components/cardsmal"
import './App.css';

class Country extends React.Component{
    state = {
        filter: "",
        data: [
            {
                id: "0",
                name: "ایتالیا",
                disc: "اموزش اشپزی به سبک ایتالیایی",
                imageSrc: "https://media.isna.ir/content/italy-2.jpg/3",
            },
            {
                id: "1",
                name: "ایران",
                disc: "اموزش اشپزی به سبک ایرانی",
                imageSrc: "https://eslahatnews.com/wp-content/uploads/2016/09/Flag_of_Iran_official.svg_.png",
            },
            {
                id: "2",
                name: "ژاپن",
                disc: "اموزش اشپزی به سبک ژاپنی",
                imageSrc: "https://cdn-tehran.wisgoon.com/dlir-s3/500x312_1541973222875951.png",
            },
            {
                id: "3",
                name: "اسپانیا",
                disc: "اموزش اشپزی به سبک اسپانیایی",
                imageSrc: "https://static1.jamaran.news/servev2/8DjjPm3j75xH/lLl5vbV5w7c,/SPAIN.jpg",
            }
        ]
    };

    handleChange = event => {
        this.setState({filter: event.target.value});
    };

    render() {
        const {filter, data} = this.state;
        const lowercasedFilter = filter.toLowerCase();
        const filteredData = data.filter(item => {
            return Object.keys(item).some(key =>
                item[key].toLowerCase().includes(lowercasedFilter)
            );
        });
        return (
            <>
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-lg-12">
                            <div className="card round shadow-sm baner-top">
                                <img src="https://media.farsnews.ir/Uploaded/Files/Images/1397/03/31/13970331000732_Test_PhotoJ.jpg" className="img-fluid round" alt=""/>
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <input value={filter} onChange={this.handleChange} className="form-control text-right col-lg-10 offset-lg-1 col-12 mt-2 rtl round border-0 border-bottom shadow-sm" placeholder="جستجو..." />
                        {filteredData.map(item => (
                            <div className="col-lg-3 col-md-4 col-12 mt-2 text-decoration-none " key={item.id}>
                                <CardActionArea className="round text-dark">
                                    <Link to={`/foods/country/${item.name}`}>
                                        <div className="card shadow-sm round text-decoration-none text-dark d-lg-inline-block d-none">
                                            <div className="card-body p-0">
                                                <img
                                                    src={item.imageSrc}
                                                    height={250}

                                                    className="w-100 round" alt=""/>
                                            </div>
                                            <div className="card-footer text-right ">
                                                <h6>{item.name}</h6>
                                                <p> {item.disc}</p>
                                            </div>
                                        </div>
                                        <CardSmal
                                            src={item.imageSrc}
                                            title={item.name}
                                        ></CardSmal>

                                    </Link>
                                </CardActionArea>
                            </div>
                        ))}
                    </div>
                </div>

            </>
        );
    }

}

export default Country;
