import React from "react";
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Link} from "react-router-dom";
import CardActionArea from '@material-ui/core/CardActionArea';
import CardSmal from "./Components/cardsmal";

class Types extends React.Component{
    state = {
        filter: "",
        data: [
            {
                id: "0",
                name: "کیک و شیرینی",
                disc: "اموزش کیک و شیرینی",
                imageSrc: "https://www.loginbrands.com/images/Gallery/10716-Birthday-Cake-and-Sweets-20.jpg",
            },
            {
                id: "1",
                name: "خورشت ها",
                disc: "اموزش خورشت ها",
                imageSrc: "https://aronfoods.com/wp-content/uploads/2019/09/%D8%AE%D9%88%D8%B1%D8%B4-%D9%82%DB%8C%D9%85%D9%87-%D8%A2%D9%84%D9%88-1024x819.jpg",
            },
            {
                id: "2",
                name: "غذا های کبابی",
                disc: "اموزش غذا های کبابی",
                imageSrc: "https://cdn.blog.safarme.com/2016/08/1018410184.jpg",
            },
            {
                id: "3",
                name: "غذا های دریایی",
                disc: "اموزش غذا های دریایی",
                imageSrc: "https://www.snapptrip.com/blog/wp-content/uploads/2018/12/%D8%B1%D8%B3%D8%AA%D9%88%D8%B1%D8%A7%D9%86-%D9%87%D8%A7%DB%8C-%DA%86%D8%A7%D8%A8%D9%87%D8%A7%D8%B1.jpg",
            }
        ]
    };
    handleChange = event => {
        this.setState({filter: event.target.value});
    };
    render() {
        const {filter, data} = this.state;
        const lowercasedFilter = filter.toLowerCase();
        const filteredData = data.filter(item => {
            return Object.keys(item).some(key =>
                item[key].toLowerCase().includes(lowercasedFilter)
            );
        });
        return (
            <>
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-lg-12">
                            <div className="card round shadow-sm baner-top">
                                <img src="https://www.helpguide.org/wp-content/uploads/fast-foods-candy-cookies-pastries-768.jpg" className="img-fluid round" alt=""/>
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <input value={filter} onChange={this.handleChange} className="form-control text-right col-lg-10 offset-lg-1 col-12 mt-2 rtl round border-0 border-bottom shadow-sm" placeholder="جستجو..." />
                        {filteredData.map(item => (
                            <div className="col-lg-3 col-md-4 col-12 mt-2 text-decoration-none " key={item.id}>
                                <CardActionArea className="round text-dark">
                                    <Link to={`/foods/country/${item.name}`}>
                                        <div className="card shadow-sm round text-decoration-none text-dark d-lg-inline-block d-none">
                                            <div className="card-body p-0">
                                                <img
                                                    src={item.imageSrc}
                                                    height={250}

                                                    className="w-100 round" alt=""/>
                                            </div>
                                            <div className="card-footer text-right ">
                                                <h6>{item.name}</h6>
                                                <p> {item.disc}</p>
                                            </div>
                                        </div>
                                        <CardSmal
                                            src={item.imageSrc}
                                            title={item.name}
                                        ></CardSmal>

                                    </Link>
                                </CardActionArea>
                            </div>
                        ))}
                    </div>
                </div>
            </>
        );
    }
}

export default Types;
