import React,{Component} from "react";
import {Link} from "react-router-dom";
import CardActionArea from '@material-ui/core/CardActionArea';
import CardSmal from "./Components/cardsmal";

class Foods extends React.Component {
    state = {
        filter: "",
        data: [
            {
                id: "0",
                name: "همبرگر",
                disc: "اموزش همبرگر",
                imageSrc: "https://www.helpguide.org/wp-content/uploads/fast-foods-candy-cookies-pastries-768.jpg",
            },
            {
                id: "1",
                name: "ماکارانی",
                disc: "اموزش ماکارانی",
                imageSrc: "https://rasamalaysia.com/wp-content/uploads/2018/08/one-pan-pasta-thumb.jpg",
            },
            {
                id: "2",
                name: "پاستا",
                disc: "اموزش پاستا",
                imageSrc: "https://static.parade.com/wp-content/uploads/2020/06/Best-pasta-recipes.jpg",
            },
            {
                id: "3",
                name: "پیتزا قارچ و مرغ",
                disc: "اموزش پیتزا قارچ و مرغ",
                imageSrc: "https://kalleh.com/book/wp-content/uploads/sites/2/2018/04/chicken-pizza.jpg",
            }
        ]
    };

    handleChange = event => {
        this.setState({filter: event.target.value});
    };

    render() {
        console.log(this.props.match.params)
        const {filter, data} = this.state;
        const lowercasedFilter = filter.toLowerCase();
        const filteredData = data.filter(item => {
            return Object.keys(item).some(key =>
                item[key].toLowerCase().includes(lowercasedFilter)
            );
        });
        return (
            <>
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-lg-12">
                            <div className="card round shadow-sm baner-top">
                                <img src="https://www.helpguide.org/wp-content/uploads/fast-foods-candy-cookies-pastries-768.jpg" className="img-fluid round" alt=""/>
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <input value={filter} onChange={this.handleChange} className="form-control text-right col-lg-10 offset-lg-1 col-12 mt-2 rtl round border-0 border-bottom shadow-sm" placeholder="جستجو..." />
                        {filteredData.map(item => (
                            <div className="col-lg-3 col-md-4 col-12 mt-2 text-decoration-none " key={item.id}>
                                <CardActionArea className="round text-dark">
                                    <Link to={`/food/${item.id}`}>
                                        <div className="card shadow-sm round text-decoration-none text-dark d-lg-inline-block d-none">
                                            <div className="card-body p-0">
                                                <img
                                                    src={item.imageSrc}
                                                    height={250}

                                                    className="w-100 round" alt=""/>
                                            </div>
                                            <div className="card-footer text-right ">
                                                <h6>{item.name}</h6>
                                                <p> {item.disc}</p>
                                            </div>
                                        </div>
                                        <CardSmal
                                            src={item.imageSrc}
                                            title={item.name}
                                        ></CardSmal>

                                    </Link>
                                </CardActionArea>
                            </div>
                        ))}
                    </div>
                </div>
            </>
        );
    }
}

export default Foods;